.auth-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffffff;
  position: relative;
  overflow: hidden;
}

.auth-container:before {
  content: '';
  position: absolute;
  bottom: -50px;
  width: 150%;
  height: 300px;
  background-color: #00F0D3;
  border-radius: 50%;
  z-index: 0;
}

.auth-container > div {
  background: white;
  padding: 40px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
  text-align: center;
  z-index: 1;
}

.auth-container h2 {
  margin-bottom: 20px;
  font-size: 24px;
  color: #4a3d57;
}

.auth-container input {
  width: 92.75%; /* Adjusted to make the input box slightly shorter */
  padding: 10px;
  margin-bottom: 15px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

.auth-container .password-container {
  display: flex;
  align-items: center;
  /* margin-bottom: 5px; */
  position: relative;
}

.auth-container .password-container input {
  padding-right: 40px; /* Adjust to accommodate the eye icon */
}

.auth-container .eye-icon {
  position: absolute;
  right: 10px;
  width: 17px; /* Adjust the size of the eye icon */
  height: 16px;
  margin-bottom: 12px; /* Add margin-bottom to the eye icon */
  cursor: pointer;
}

.auth-container .login-options {
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  margin-bottom: 15px;
}

.auth-container .login-options .remember-me {
  display: flex;
  /* align-items: center; */
  font-size: 12px; /* Set the font size for Remember me */
  margin-top: 0px;
}

.auth-container .login-options .remember-me input {
  margin-top: 1px;
  margin-right: 10px;
}

.auth-container .forgot-password-link {
  font-size: 12px; /* Set the font size for Forgot Password? */
  color: #4a3d57;
  cursor: pointer;
  margin-top: -15px;
  margin-left: 70px;
  background: none; /* Remove background */
  border: none; /* Remove border */
}

.auth-container .forgot-password-link:hover {
  text-decoration: underline;
}

.auth-container button {
  width: 100%;
  padding: 10px;
  background-color: #26a69a;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  margin-top: 10px;
}

.auth-container button:hover {
  background-color: #1e857c;
}

.auth-container .error {
  color: red;
  margin-bottom: 10px;
}

.terms-container {
  display: flex;
  /* align-items: center; */
  justify-content: flex-start;
  font-size: 12px;
  margin-bottom: 20px;
  margin-left: 270px;
}

.terms-container input[type="checkbox"] {
  margin-left: -277px;
}

.terms-container span {
  color: #4a3d57;
  /* white-space: nowrap;  */
}

.signup-container input {
  margin-bottom: 10px; /* Ensure equal spacing between all input boxes */
}

.auth-container input {
  font-size: 16px;
}

.password-container input {
  font-size: 16px;
}
