.chat-list-page {
  height: 100vh;
  background: linear-gradient(to right, #61DBCC, #ffffff);
  display: flex;
  flex-direction: column;
}

.top-bar {
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #000;
  position: relative;
  margin-top: 15px;
}

.back-icon {
  width: 24px;
  height: 24px;
  position: absolute;
  left: 20px;
  margin-top: -40px;
  cursor: pointer;
}

.title {
  font-size: 24px;
  margin: 0;
  margin-bottom: 20px;
}

.edit-icon {
  width: 24px;
  height: 24px;
  position: absolute;
  right: 35px;
  top: 15px;
  cursor: pointer;
}

.search-container {
  display: flex;
  align-items: center;
  width: 90%;
  margin: 20px 0;
  background-color: #fff;
  border-radius: 20px;
  padding: 5px 10px;
}

.search-icon {
  width: 20px;
  height: 20px;
  margin-right: 10px;
}

.search-bar {
  width: 100%;
  padding: 5px;
  border: none;
  background: none;
  color: #000;
  font-size: 16px;
}

.search-bar::placeholder {
  color: #999;
}

.chat-list {
  flex: 1;
  overflow-y: auto;
  margin-top: 10px;
}

.chat-item {
  display: flex;
  align-items: center;
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.chat-icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin-left: 20px;
}

.chat-info {
  flex: 1;
}

.chat-name {
  font-size: 16px;
  margin-bottom: 15px;
  font-weight: bold;
}

.chat-message {
  font-size: 14px;
  margin-top: 40px;
  margin-left: -115px;
  color: #666;
}

.chat-time-unread {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}

.chat-time {
  font-size: 12px;
  color: #999;
}

.chat-unread {
  background-color: #b39ddb;
  color: #ffffff;
  border-radius: 10px;
  padding: 2px 6px;
  font-size: 12px;
  margin-top: 5px;
}

.bottom-bar {
  display: flex;
  justify-content: space-around;
  padding: 10px 0;
  background-color: #ffffff;
  border-top: 1px solid #ddd;
  border-radius: 20px;
  margin: 10px 20px;
}

.bottom-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 14px;
  color: #666;
}

.bottom-icon-image {
  width: 24px;
  height: 24px;
  margin-bottom: 5px;
}

.bottom-icon.chats {
  color: #000;
  font-weight: bold;
}
