.chat-screen {
    display: flex;
    flex-direction: column;
    height: 100vh;
    background: linear-gradient(to right, #1e857c, #ffffff); /* Gradient from left to right */
}

.chat-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    background: white;
    border-bottom: 1px solid #ddd;
}

.back-icon {
    width: 24px;
    height: 24px;
    cursor: pointer;
}

.chat-info {
    display: flex;
    align-items: center;
    flex: 1;
    margin-top: 2px;
    margin-left: 40px; /* Increased margin to move the group icon and room name to the right */
}

.group-icon {
    width: 24px;
    height: 24px;
    margin-right: 10px;
}

.room-name {
    font-size: 18px;
    margin: 0;
    color: #4a3d57;
}

.chat-actions button {
    margin-left: 10px;
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
}

.chat-messages {
    flex: 1;
    padding: 10px;
    overflow-y: auto;
}

.message {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: white;
    border-radius: 10px;
    margin-bottom: 10px;
    max-width: 70%;
}

.message.sent {
    align-self: flex-end;
    background: #DCF8C6;
}

.message.received {
    align-self: flex-start;
    background: #fff;
}

.time {
    font-size: 10px;
    color: gray;
    margin-left: 5px;
    align-self: flex-end;
}

.chat-input {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ddd;
    background: white;
}

.chat-input input {
    flex: 1;
    padding: 10px;
    border-radius: 20px;
    border: 1px solid #ddd;
}

.send-button {
    background: none;
    border: none;
    font-size: 20px;
    cursor: pointer;
    margin-left: 10px;
    color: #1e857c;
}
