body.light {
    --background-color: #ffffff;
    --text-color: #000000;
    --input-background: #f0f0f0;
    --button-background: #61DBCC;
    --button-text-color: #ffffff;
  }
  
  body.dark {
    --background-color: #333333;
    --text-color: #ffffff;
    --input-background: #555555;
    --button-background: #4A3D57;
    --button-text-color: #ffffff;
  }
  
  .profile-edit-container {
    height: 100vh;
    background: var(--background-color);
    color: var(--text-color);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
  
  .top-bar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    position: relative;
  }
  
  .back-icon, .check-icon {
    width: 24px;
    height: 24px;
    cursor: pointer;
  }
  
  .back-icon {
    position: absolute;
    left: 20px;
    top: 15px;
  }
  
  .check-icon {
    position: absolute;
    right: 20px;
    top: 15px;
  }
  
  .profile-pic-container {
    margin: 20px 0;
    position: relative;
  }
  
  .profile-pic {
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background: var(--input-background);
  }
  
  .camera-icon {
    position: absolute;
    bottom: 0;
    right: 0;
    background: var(--button-background);
    color: var(--button-text-color);
    border-radius: 50%;
    padding: 5px;
  }
  
  .input-container {
    width: 100%;
    margin-bottom: 15px;
  }
  
  .input-container label {
    display: block;
    margin-bottom: 5px;
  }
  
  .input-container input, .input-container textarea {
    width: 100%; /* Use full width */
    padding: 10px;
    background: var(--input-background);
    color: var(--text-color);
    border: 1px solid #ddd;
    border-radius: 5px;
    box-sizing: border-box; /* Ensure padding is included in the width */
  }
  
  .theme-toggle {
    margin-top: 20px;
  }
  
  .theme-toggle button {
    padding: 10px 20px;
    background: var(--button-background);
    color: var(--button-text-color);
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .theme-toggle button:hover {
    background: #3a2d45;
  }
  
  .logout-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .logout-button {
    padding: 10px 20px;
    background-color: #ff6b6b;
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 30px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .logout-button:hover {
    background-color: #e65a5a;
  }
  