/* src/components/WelcomePage.css */

.welcome-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #ffffff;
  position: relative;
  overflow: hidden;
}

.welcome-container:before {
  content: '';
  position: absolute;
  bottom: -50px;
  width: 150%;
  height: 300px;
  background-color: #00F0D3;
  border-radius: 50%;
  z-index: 0;
}

.welcome-content {
  text-align: center;
  z-index: 1; /* Ensure content is above the curve */
}

.logo {
  width: 200px;
  height: 200px;
}

h1 {
  font-size: 28px;
  font-weight: bold;
  color: #4A3D57;
  margin-top: 30px;
}

p {
  font-size: 18px;
  color: #4A3D57;
  margin-top: 10px;
}

.welcome-button {
  background-color: white;
  color: black;
  font-size: 18px;
  font-weight: bold;
  padding: 15px 30px;
  border: none;
  border-radius: 20px;
  margin-top: 40px;
  cursor: pointer;
  z-index: 1; /* Ensure the button is above the curve */
}

.welcome-button:hover {
  background-color: #f0f0f0;
}
